.profile__info {
  display: grid;
  grid-template-columns: 294px 24px;
  gap: 7px 18px;
  flex-grow: 1;
  margin-right: 30px;
}

@media screen and (max-width: 540px) {
  .profile__info::before {
    content: "";
  }

  .profile__info {
    grid-template-columns: 16px 189px 16px;
    column-gap: 10px;
    margin: 0;
  }
}